    MainHeader,
    AboutSection,
    SkillsSection,
    CareerSection,
    // ExperienceSection,
    // EducationSection,
    // InterestsSection
  },
  data() {
    return {
      isMenuVisible: false
    };
  },
  methods: {
    handleScrollRequest(sectionName) {
      this.$refs[sectionName].$el.scrollIntoView({ behavior: 'smooth' });
    },
    updateMenuVisibility(status) {
      this.isMenuVisible = status;
    }
  }
}
</script>

