<template>
  <section class="education">
    <h2 class="education-title">Career</h2>

    <!-- PC -->
    <table class="education-table">
      <thead>
        <tr>
          <th>TYPE</th>
          <th>DATE</th>
          <th>DISCRIOTION</th>
          <th>ORGANIZTION</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(career, i) in Career" :key="i">
          <td>{{ career.TYPE }}</td>
          <td>{{ career.DATE }}</td>
          <td>{{ career.DISCRIOTION }}</td>
          <td>{{ career.ORGANIZTION }}</td>
        </tr>
      </tbody>
    </table>


    <!-- Mobile -->
    <div v-for="(career, i) in displayedCard" :key="i">
      <div class="grid">
        <div class="card">
            <h4>{{ career.TYPE }}</h4>
            <p><strong>Date:</strong> {{ career.DATE }}</p>
            <p><strong>Description:</strong> {{ career.DISCRIOTION }}</p>
            <p><strong>Organization:</strong> {{ career.ORGANIZTION }}</p>
          <!-- <div class="shine"></div> -->
          <div class="background">
            <div class="tiles">
              <div class="tile tile-1"></div>
              <div class="tile tile-2"></div>
              <div class="tile tile-3"></div>
              <div class="tile tile-4"></div>

              <div class="tile tile-5"></div>
              <div class="tile tile-6"></div>
              <div class="tile tile-7"></div>
              <div class="tile tile-8"></div>

              <div class="tile tile-9"></div>
              <div class="tile tile-10"></div>
            </div>

            <div class="line line-1"></div>
            <div class="line line-2"></div>
            <div class="line line-3"></div>
          </div>
        </div>
      </div>
    </div>

    <div class="nav-buttons">
      <button class="btn previous" @click="prevSlide">Previous</button>
      <button class="btn next" @click="nextSlide">Next</button>
    </div>
    


  </section>
</template>

<script>
export default {
  data() {
    return {
      Career: [
        { TYPE: 'Education', DATE: '2019.02 - 2019.7', DISCRIOTION: 'Spring Programming', ORGANIZTION: 'KITRI' },
        { TYPE: 'Employment', DATE: '2019.08 - 2021.12', DISCRIOTION: 'RPA', ORGANIZTION: 'KPCNC' },
        { TYPE: 'Education', DATE: '2021.10 - 2021.12', DISCRIOTION: 'Application Software Engineering', ORGANIZTION: 'THEHOEUN' },
        { TYPE: 'Employment', DATE: '2022.01 - 2022.~', DISCRIOTION: 'Anyframe(framework)', ORGANIZTION: 'KPCNC' },
        { TYPE: 'Education', DATE: '2023.07 - 2023.~', DISCRIOTION: 'Vue', ORGANIZTION: 'GreenComputerArt' }
      ],
      currentSlide: 0  
    };
  },
  computed: {
    displayedCard() {
      return [this.Career[this.currentSlide]]; // 현재 슬라이드의 카드만 반환
    }
  },
  methods: {
    nextSlide() {
      if (this.currentSlide < this.Career.length - 1) {
        this.currentSlide++;
      }
    },
    prevSlide() {
      if (this.currentSlide > 0) {
        this.currentSlide--;
      }
    }
  }
};
</script>


<style>
