<template>

  <button class="toggle" @click="toggleMenu()">
    <img class="menu-toggle" :src="menuImgSrc" alt="ProFileImgFail">
  </button>

  
  <nav class="menu-bar" v-bind:class="{ 'visible': menuVisible }">
    <div class="index">
      <!-- 프로필 이미지 -->
      <div class="img">
        <img id="profile-img" :src="profileImgSrc" alt="ProFileImgFail">
      </div>
      <br>
      <!-- 목차 내용 -->
      <h3 class="section-list" @click="emitScrollRequest('about')">ABOUT</h3>
      <h3 class="section-list" @click="emitScrollRequest('skills')">SKILLS</h3>
      <h3 class="section-list" @click="emitScrollRequest('career')">CAREER</h3>
      <!-- <h3 class="section-list" @click="emitScrollRequest('experience')">EXPERIENCE</h3> -->
      <!-- <h3 class="section-list" @click="emitScrollRequest('education')">EDUCATION</h3> -->
      <!-- <h3 class="section-list" @click="emitScrollRequest('interests')">INTERESTS</h3> -->
    </div>
  </nav>

</template>
  
<script>

export default {
  data() {
    return {
      menuVisible: false, // 이 변수로 메뉴의 표시 상태를 관리
      profileImgSrc: require('@/images/profile.jpg'),
      menuImgSrc: require('@/images/menu-toggle.png'),
    };
  },
  methods: {
    toggleMenu() {
      this.menuVisible = !this.menuVisible;
      this.$emit('menuVisibilityChanged', this.menuVisible);
    },
    emitScrollRequest(section) {
      this.$emit('requestScroll', section);
    },
  }
}
</script>
  
<style scoped>
body {
  margin: 0;
}

/* 프로필 이미지 */
.img {
  justify-content: center;
}

#profile-img {
  width:50%;
  height: 50%;
  /* border: 1px solid #2B3856; */
  transform: scale(1);
  /* 이미지 사이즈 고정 */
  border-radius: 70%;
}

.menu-bar {
  position: fixed;
  border: 0;
  background-color: rgb(171, 177, 255);
  /* background-color: rgb(163, 162, 80); */
  /* background-color: rgb(53, 90, 139); */
  /* background-color: rgb(74, 129, 72); */
  display: block;
  /* width: calc(7rem + 10vw); */
  width: 20%;
  /* height: 100vh; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.index {
  text-align: center;
  display: flex;
  flex-direction: column;
}

/* 목차 */
.index h3 {
  color: white;
  opacity: 0.9;
  text-decoration: none;
  cursor: pointer;
}

/* 목차 각 항목 반응형 커서 */
.index h3:hover {
  font-size: 1.1em;
  opacity: 1;
  text-shadow: 0.7px 0.7px 0.7px #f5dcff;
}

h3 {
    display: block;
    font-size: 1.17em;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}

.toggle {
  display: none;
}

.menu-toggle {
  display: none;
  width:50%;
  height: 50%;
  /* border: 1px solid #2B3856;
  border-radius: 70%; */
  /* 이미지 사이즈 고정 */
  /* transform: scale(1); */
}

.toggle img:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1); /* 크롬 */
  -moz-transform: scale(1.1); /* 파이어폭스 */
  -o-transform: scale(1.1); /* 오페라 */
  z-index: 1;
  align-items: center;
}



/* MEDIA */
@media screen and (max-width: 1024px) {
  
  button {
    position: absolute;
    top: 20px;
    left: 20px;
  }
  
  .toggle {
    display: block;
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 10;  /* 메뉴 위에 위치하도록 설정 */
  }

  .menu-toggle{
    display: block;
    top: 20px;
    left: 20px;
    width: 50px;
    height: 50px;
  }

  .menu-bar {
    left: -25%;
    transition: left 0.3s;
  }
  .menu-bar.visible {
    left: 0;
  }

}
</style>
  